body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.pressDown {
  background-color: rgb(153, 94, 209);
  padding: 10px 30px;
  color: #fff;
  border-radius: 5px;
  border-bottom: 4px solid rgb(153, 94, 209);
  transition: 0.1s;
  cursor: pointer;
}

.hey{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size:30px;
}
.simple_line{
  font-weight: bolder;
}

.name{
  font-family:monospace;
  font-size:4em;
}
.myname{
  text-decoration:underline;
text-decoration-color: #071a52;
color: #071a52;
}

.nav-color{
  background-color: #071a52;
}

.freelancer{
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  color:#303a52;
  font-weight: bolder;
}

@font-face{
  font-family:"RobotoItalic";
  src:url(./assets/fonts/Roboto-BlackItalic.ttf);
  src:url(./assets/fonts/Roboto-BlackItalic.ttf)format("truetype");
}

@font-face{
  font-family:"RobotoMedium";
  src:url(./assets/fonts/Roboto-MediumItalic.ttf);
  src:url(./assets/fonts/Roboto-MediumItalic.ttf)format("truetype");
}


.para{
  text-align:center;
  text-indent: inherit;
  font-family:"RobotoMedium";
}
.head{
  font-family:"RobotoItalic";
}

.btn-cv{
  background-color:#071a52;
  color: #fff;
} 
 .service{
  background-color: #e2f3f5;
} 

.service:hover{
  box-shadow:
  0 4.5px 3.6px rgba(0, 0, 0, 0.08),
  0 12.5px 10px rgba(0, 0, 0, 0.115),
  0 30.1px 24.1px rgba(0, 0, 0, 0.15),
  0 100px 80px rgba(0, 0, 0, 0.23)
;
transition-delay: 0.5ms;
}


.service-2:hover{
  box-shadow:
  0 4.5px 3.6px rgba(0, 0, 0, 0.08),
  0 12.5px 10px rgba(0, 0, 0, 0.115),
  0 30.1px 24.1px rgba(0, 0, 0, 0.15),
  0 100px 80px rgba(0, 0, 0, 0.23)
;
transition-delay: 0.5ms;
}

.lead{
  font-family:"RobotoMedium";
}

.service-head{
  font-family:"RobotoItalic";
}
.service-2{
  background-color:#dee1ec;
}

.skill{
  border: 1px solid #303a52;
  background-color: #303a52;
  border-radius:2rem;
  color: #fff;
}
.skillDiv{
  margin:0px auto;
}

.test{
  overflow-y:scroll;
  max-height: 300px;
}

.footer{
  background-color: #071a52;
}

